import React, { useState, useRef, useEffect } from 'react';
import { Card, Row, Col, Form, Button, Table, Alert } from 'react-bootstrap';
import { useForm } from 'react-hook-form';
import Chart from 'chart.js/auto';
import api from '../services/api';

const SensitivityAnalysis = ({ onError }) => {
  const [result, setResult] = useState(null);
  const [loading, setLoading] = useState(false);
  const chartRef = useRef(null);
  const chartInstance = useRef(null);

  const { register, handleSubmit, watch, setValue, formState: { errors } } = useForm({
    defaultValues: {
      method: 'equal_annuity',
      pv: 1000000,
      annual_rate: 8.00,  // 
      periods: 36,
      frequency: 12,
      years: 3,
      equipment_price: 1000000,
      down_payment: 200000,
      guarantee: 0,
      guarantee_mode: '',
      rate_variation: 1.00,  // 
      period_variation: 6,
      pv_variation: 100000
    }
  });

  const watchEquipmentPrice = watch('equipment_price');
  const watchDownPayment = watch('down_payment');
  const watchFrequency = watch('frequency');
  const watchYears = watch('years');

  // 
  useEffect(() => {
    const equipmentPrice = parseFloat(watchEquipmentPrice) || 0;
    const downPayment = parseFloat(watchDownPayment) || 0;
    const pv = equipmentPrice - downPayment;
    setValue('pv', pv);
  }, [watchEquipmentPrice, watchDownPayment, setValue]);

  // 
  useEffect(() => {
    const years = parseFloat(watchYears) || 3;
    const frequency = parseInt(watchFrequency) || 12;
    const periods = years * frequency;
    setValue('periods', periods);
  }, [watchYears, watchFrequency, setValue]);

  // 
  const createChart = (data) => {
    if (chartInstance.current) {
      chartInstance.current.destroy();
    }

    if (!chartRef.current) return;

    const ctx = chartRef.current.getContext('2d');
    chartInstance.current = new Chart(ctx, {
      type: 'bar',
      data: {
        labels: data.map(item => item.parameter),
        datasets: [{
          label: '',
          data: data.map(item => Math.abs(item.sensitivity)),
          backgroundColor: data.map(item => {
            const abs = Math.abs(item.sensitivity);
            if (abs > 1) return 'rgba(220, 53, 69, 0.8)';
            if (abs > 0.5) return 'rgba(255, 193, 7, 0.8)';
            return 'rgba(40, 167, 69, 0.8)';
          }),
          borderColor: data.map(item => {
            const abs = Math.abs(item.sensitivity);
            if (abs > 1) return 'rgba(220, 53, 69, 1)';
            if (abs > 0.5) return 'rgba(255, 193, 7, 1)';
            return 'rgba(40, 167, 69, 1)';
          }),
          borderWidth: 1
        }]
      },
      options: {
        responsive: true,
        maintainAspectRatio: false,
        scales: {
          y: {
            beginAtZero: true,
            title: {
              display: true,
              text: ' (||)'
            }
          },
          x: {
            title: {
              display: true,
              text: ''
            }
          }
        },
        plugins: {
          legend: {
            display: true,
            position: 'top'
          },
          title: {
            display: true,
            text: ''
          }
        }
      }
    });
  };

  const onSubmit = async (data) => {
    setLoading(true);
    try {
      // 
      const processedData = {
        ...data,
        annual_rate: data.annual_rate / 100,  // 
        rate_variation: data.rate_variation / 100  // 
      };

      const response = await api.post('/sensitivity_analysis', processedData);
      setResult(response.data);
      
      // 
      if (response.data.sensitivity_analysis) {
        setTimeout(() => {
          createChart(response.data.sensitivity_analysis);
        }, 100);
      }
    } catch (error) {
      console.error(':', error);
      onError?.(error.response?.data?.error || '');
    } finally {
      setLoading(false);
    }
  };

  // 
  useEffect(() => {
    return () => {
      if (chartInstance.current) {
        chartInstance.current.destroy();
      }
    };
  }, []);

  return (
    <div>
      <Card className="mb-4">
        <Card.Header>
          <h5 className="mb-0">
            <i className="fas fa-chart-line me-2"></i>
            
          </h5>
        </Card.Header>
        <Card.Body>
          <Form onSubmit={handleSubmit(onSubmit)}>
            {/*  */}
            <div className="form-section">
              <h6><i className="fas fa-cog me-2"></i></h6>
              
              <Row className="mb-3">
                <Col md={6}>
                  <Form.Label></Form.Label>
                  <Form.Select
                    {...register('method', { required: '' })}
                    isInvalid={!!errors.method}
                  >
                    <option value="equal_annuity"></option>
                    <option value="equal_principal"></option>
                  </Form.Select>
                  {errors.method && (
                    <Form.Control.Feedback type="invalid">
                      {errors.method.message}
                    </Form.Control.Feedback>
                  )}
                </Col>
              </Row>

              <Row className="mb-3">
                <Col md={6}>
                  <Form.Label></Form.Label>
                  <Form.Control
                    type="number"
                    step="1000"
                    {...register('equipment_price', { 
                      required: '',
                      min: { value: 1, message: '0' }
                    })}
                    isInvalid={!!errors.equipment_price}
                  />
                  {errors.equipment_price && (
                    <Form.Control.Feedback type="invalid">
                      {errors.equipment_price.message}
                    </Form.Control.Feedback>
                  )}
                </Col>
                <Col md={6}>
                  <Form.Label></Form.Label>
                  <Form.Control
                    type="number"
                    step="1000"
                    {...register('down_payment', { 
                      required: '',
                      min: { value: 0, message: '' }
                    })}
                    isInvalid={!!errors.down_payment}
                  />
                  {errors.down_payment && (
                    <Form.Control.Feedback type="invalid">
                      {errors.down_payment.message}
                    </Form.Control.Feedback>
                    )}
                </Col>
              </Row>

              <Row className="mb-3">
                <Col md={6}>
                  <Form.Label></Form.Label>
                  <Form.Control
                    type="number"
                    {...register('pv')}
                    readOnly
                    className="bg-light"
                  />
                  <Form.Text className="text-muted">
                     - 
                  </Form.Text>
                </Col>
                <Col md={6}>
                  <Form.Label> (%)</Form.Label>
                  <Form.Control
                    type="number"
                    step="0.01"
                    {...register('annual_rate', { 
                      required: '',
                      min: { value: 0.01, message: '0' }
                    })}
                    isInvalid={!!errors.annual_rate}
                  />
                  {errors.annual_rate && (
                    <Form.Control.Feedback type="invalid">
                      {errors.annual_rate.message}
                    </Form.Control.Feedback>
                  )}
                </Col>
              </Row>

              <Row className="mb-3">
                <Col md={4}>
                  <Form.Label></Form.Label>
                  <Form.Control
                    type="number"
                    step="0.1"
                    {...register('years', { 
                      required: '',
                      min: { value: 0.1, message: '0' }
                    })}
                    isInvalid={!!errors.years}
                  />
                  {errors.years && (
                    <Form.Control.Feedback type="invalid">
                      {errors.years.message}
                    </Form.Control.Feedback>
                  )}
                </Col>
                <Col md={4}>
                  <Form.Label></Form.Label>
                  <Form.Select
                    {...register('frequency', { required: '' })}
                    isInvalid={!!errors.frequency}
                  >
                    <option value="12"></option>
                    <option value="4"></option>
                    <option value="2"></option>
                    <option value="1"></option>
                  </Form.Select>
                  {errors.frequency && (
                    <Form.Control.Feedback type="invalid">
                      {errors.frequency.message}
                    </Form.Control.Feedback>
                  )}
                </Col>
                <Col md={4}>
                  <Form.Label></Form.Label>
                  <Form.Control
                    type="number"
                    {...register('periods')}
                    readOnly
                    className="bg-light"
                  />
                  <Form.Text className="text-muted">
                    
                  </Form.Text>
                </Col>
              </Row>
            </div>

            {/*  */}
            <div className="form-section">
              <h6><i className="fas fa-shield-alt me-2"></i></h6>
              
              <Row className="mb-3">
                <Col md={6}>
                  <Form.Label></Form.Label>
                  <Form.Control
                    type="number"
                    step="1000"
                    {...register('guarantee', { 
                      min: { value: 0, message: '' }
                    })}
                    isInvalid={!!errors.guarantee}
                  />
                  {errors.guarantee && (
                    <Form.Control.Feedback type="invalid">
                      {errors.guarantee.message}
                    </Form.Control.Feedback>
                  )}
                </Col>
                <Col md={6}>
                  <Form.Label></Form.Label>
                  <Form.Select
                    {...register('guarantee_mode')}
                  >
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                  </Form.Select>
                </Col>
              </Row>
            </div>

            {/*  */}
            <div className="form-section">
              <h6><i className="fas fa-sliders-h me-2"></i></h6>
              
              <Row className="mb-3">
                <Col md={4}>
                  <Form.Label> (%)</Form.Label>
                  <Form.Control
                    type="number"
                    step="0.01"
                    {...register('rate_variation', { 
                      required: '',
                      min: { value: 0.01, message: '0' }
                    })}
                    isInvalid={!!errors.rate_variation}
                    placeholder="1.00"
                  />
                  <Form.Text className="text-muted">1.001%</Form.Text>
                  {errors.rate_variation && (
                    <Form.Control.Feedback type="invalid">
                      {errors.rate_variation.message}
                    </Form.Control.Feedback>
                  )}
                </Col>
                <Col md={4}>
                  <Form.Label></Form.Label>
                  <Form.Control
                    type="number"
                    {...register('period_variation', { 
                      required: '',
                      min: { value: 1, message: '1' }
                    })}
                    isInvalid={!!errors.period_variation}
                  />
                  <Form.Text className="text-muted"></Form.Text>
                  {errors.period_variation && (
                    <Form.Control.Feedback type="invalid">
                      {errors.period_variation.message}
                    </Form.Control.Feedback>
                  )}
                </Col>
                <Col md={4}>
                  <Form.Label></Form.Label>
                  <Form.Control
                    type="number"
                    step="1000"
                    {...register('pv_variation', { 
                      required: '',
                      min: { value: 1000, message: '1000' }
                    })}
                    isInvalid={!!errors.pv_variation}
                  />
                  <Form.Text className="text-muted"></Form.Text>
                  {errors.pv_variation && (
                    <Form.Control.Feedback type="invalid">
                      {errors.pv_variation.message}
                    </Form.Control.Feedback>
                  )}
                </Col>
              </Row>
            </div>

            <div className="text-center">
              <Button
                type="submit"
                variant="primary"
                size="lg"
                disabled={loading}
              >
                {loading ? (
                  <>
                    <i className="fas fa-spinner fa-spin me-2"></i>
                    ...
                  </>
                ) : (
                  <>
                    <i className="fas fa-chart-line me-2"></i>
                    
                  </>
                )}
              </Button>
            </div>
          </Form>
        </Card.Body>
      </Card>

      {/*  */}
      {result && (
        <>
          {/*  */}
          <Card className="mb-4">
            <Card.Header>
              <h5 className="mb-0">
                <i className="fas fa-bullseye me-2"></i>
                
              </h5>
            </Card.Header>
            <Card.Body>
              <Row>
                <Col md={3}>
                  <div className="text-center p-3 bg-light rounded">
                    <h6 className="text-muted mb-1"></h6>
                    <h4 className="mb-0 text-primary">
                      {result.base_payment?.toLocaleString() || 0}
                    </h4>
                  </div>
                </Col>
                <Col md={3}>
                  <div className="text-center p-3 bg-light rounded">
                    <h6 className="text-muted mb-1"></h6>
                    <h4 className="mb-0 text-success">
                      {result.base_total_payment?.toLocaleString() || 0}
                    </h4>
                  </div>
                </Col>
                <Col md={3}>
                  <div className="text-center p-3 bg-light rounded">
                    <h6 className="text-muted mb-1"></h6>
                    <h4 className="mb-0 text-info">
                      {((result.base_irr || 0) * 100).toFixed(2)}%
                    </h4>
                  </div>
                </Col>
                <Col md={3}>
                  <div className="text-center p-3 bg-light rounded">
                    <h6 className="text-muted mb-1"></h6>
                    <h4 className="mb-0 text-warning">
                      {result.base_total_interest?.toLocaleString() || 0}
                    </h4>
                  </div>
                </Col>
              </Row>
            </Card.Body>
          </Card>

          {/*  */}
          {result.sensitivity_analysis && (
            <Card className="mb-4">
              <Card.Header>
                <h5 className="mb-0">
                  <i className="fas fa-table me-2"></i>
                  
                </h5>
              </Card.Header>
              <Card.Body>
                <div className="table-responsive">
                  <Table striped bordered hover>
                    <thead>
                      <tr>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                      </tr>
                    </thead>
                    <tbody>
                      {result.sensitivity_analysis.map((item, index) => (
                        <tr key={index}>
                          <td>{item.parameter}</td>
                          <td className={item.change > 0 ? 'text-success' : 'text-danger'}>
                            {item.change > 0 ? '+' : ''}{item.change}
                            {item.parameter.includes('') ? '%' : 
                             item.parameter.includes('') ? '' : ''}
                          </td>
                          <td>{item.payment?.toLocaleString() || 0}</td>
                          <td className={item.payment_change > 0 ? 'text-danger' : 'text-success'}>
                            {item.payment_change > 0 ? '+' : ''}{item.payment_change?.toLocaleString() || 0}
                          </td>
                          <td className={item.change_rate > 0 ? 'text-danger' : 'text-success'}>
                            {item.change_rate > 0 ? '+' : ''}{(item.change_rate || 0).toFixed(2)}%
                          </td>
                          <td>
                            <span className={`badge ${Math.abs(item.sensitivity || 0) > 1 ? 'bg-danger' : 
                                                     Math.abs(item.sensitivity || 0) > 0.5 ? 'bg-warning' : 'bg-success'}`}>
                              {(item.sensitivity || 0).toFixed(2)}
                            </span>
                          </td>
                        </tr>
                      ))}
                    </tbody>
                  </Table>
                </div>
              </Card.Body>
            </Card>
          )}

          {/*  */}
          <Card className="mb-4">
            <Card.Header>
              <h5 className="mb-0">
                <i className="fas fa-chart-bar me-2"></i>
                
              </h5>
            </Card.Header>
            <Card.Body>
              <div className="chart-container" style={{ height: '400px' }}>
                <canvas 
                  ref={chartRef}
                  style={{ maxHeight: '100%', width: '100%' }}
                ></canvas>
              </div>
            </Card.Body>
          </Card>

          {/*  */}
          {result.sensitivity_analysis && (
            <Alert variant="info">
              <Row>
                <Col md={6}>
                  <h6></h6>
                  <ul className="mb-0">
                    {result.sensitivity_analysis.map((item, index) => {
                      if (Math.abs(item.sensitivity || 0) > 1) {
                        return (
                          <li key={index}>
                            <strong>{item.parameter}</strong>:  {(item.sensitivity || 0).toFixed(2)}
                          </li>
                        );
                      }
                      return null;
                    })}
                  </ul>
                </Col>
                <Col md={6}>
                  <h6></h6>
                  <p className="mb-0">
                    
                    
                  </p>
                </Col>
              </Row>
            </Alert>
          )}
        </>
      )}
    </div>
  );
};

export default SensitivityAnalysis;
