import React, { useState } from 'react';
import { Card, Row, Col, Form, Button, Table, Alert, InputGroup, Badge } from 'react-bootstrap';
import { useForm } from 'react-hook-form';
import api from '../services/api';

const ReverseCalculation = ({ onError }) => {
  const [result, setResult] = useState(null);
  const [loading, setLoading] = useState(false);

  const { register, handleSubmit, watch, setValue, formState: { errors } } = useForm({
    defaultValues: {
      calculation_type: 'find_rate', // 'find_rate'  'find_irr'
      method: 'equal_annuity',
      pv: 1000000,
      periods: 36,
      frequency: 12,
      years: 3,
      equipment_price: 1000000,
      down_payment: 200000,
      guarantee: 0,
      guarantee_mode: '',
      target_pmt: 30000, // 
      target_irr: 8.00   // IRR(%)
    }
  });

  const watchEquipmentPrice = watch('equipment_price');
  const watchDownPayment = watch('down_payment');
  const watchFrequency = watch('frequency');
  const watchYears = watch('years');
  const watchCalculationType = watch('calculation_type');

  // 
  React.useEffect(() => {
    const equipmentPrice = parseFloat(watchEquipmentPrice) || 0;
    const downPayment = parseFloat(watchDownPayment) || 0;
    const pv = equipmentPrice - downPayment;
    setValue('pv', pv);
  }, [watchEquipmentPrice, watchDownPayment, setValue]);

  // 
  React.useEffect(() => {
    const years = parseFloat(watchYears) || 3;
    const frequency = parseInt(watchFrequency) || 12;
    const periods = years * frequency;
    setValue('periods', periods);
  }, [watchYears, watchFrequency, setValue]);

  const onSubmit = async (data) => {
    setLoading(true);
    try {
      const formData = {
        calculation_type: data.calculation_type,
        method: data.method,
        pv: parseFloat(data.pv),
        periods: parseInt(data.periods),
        frequency: parseInt(data.frequency),
        years: parseFloat(data.years),
        guarantee: parseFloat(data.guarantee) || 0,
        guarantee_mode: data.guarantee_mode,
        target_pmt: data.calculation_type === 'find_rate' ? parseFloat(data.target_pmt) : undefined,
        target_irr: data.calculation_type === 'find_irr' ? parseFloat(data.target_irr) / 100 : undefined
      };

      const response = await api.post('/reverse_calculate', formData);
      setResult(response.data.data);
    } catch (error) {
      onError(error.response?.data?.message || '');
    } finally {
      setLoading(false);
    }
  };

  const formatCurrency = (amount) => {
    return new Intl.NumberFormat('zh-CN', {
      style: 'currency',
      currency: 'CNY',
      minimumFractionDigits: 2
    }).format(amount || 0);
  };

  const formatPercent = (rate) => {
    return `${((rate || 0) * 100).toFixed(4)}%`;
  };

  const methodOptions = [
    { value: 'equal_annuity', label: '' },
    { value: 'equal_principal', label: '' },
    { value: 'flat_rate', label: '' }
  ];

  const frequencyOptions = [
    { value: 12, label: '' },
    { value: 4, label: '' },
    { value: 2, label: '' },
    { value: 1, label: '' }
  ];

  const guaranteeModeOptions = [
    { value: '', label: '' },
    { value: '', label: '' },
    { value: '', label: '' }
  ];

  return (
    <div>
      {/*  */}
      <Card className="mb-4">
        <Card.Header className="bg-info text-white">
          <h5 className="mb-0">
            <i className="fas fa-exchange-alt me-2"></i>
            
          </h5>
        </Card.Header>
        <Card.Body>
          <Form onSubmit={handleSubmit(onSubmit)} className="reverse-calculation-form">
            {/*  */}
            <div className="form-section">
              <h6><i className="fas fa-target me-2"></i></h6>
              
              <Row className="mb-3">
                <Col>
                  <Form.Label></Form.Label>
                  <Form.Select
                    {...register('calculation_type', { required: '' })}
                    isInvalid={!!errors.calculation_type}
                  >
                    <option value="find_rate"></option>
                    <option value="find_irr">IRR</option>
                  </Form.Select>
                  {errors.calculation_type && (
                    <Form.Control.Feedback type="invalid">
                      {errors.calculation_type.message}
                    </Form.Control.Feedback>
                  )}
                </Col>
              </Row>

              <Row className="mb-3">
                <Col>
                  <Form.Label></Form.Label>
                  <Form.Select
                    {...register('method', { required: '' })}
                    isInvalid={!!errors.method}
                  >
                    {methodOptions.map(option => (
                      <option key={option.value} value={option.value}>
                        {option.label}
                      </option>
                    ))}
                  </Form.Select>
                  {errors.method && (
                    <Form.Control.Feedback type="invalid">
                      {errors.method.message}
                    </Form.Control.Feedback>
                  )}
                </Col>
              </Row>
            </div>

            {/*  */}
            <div className="form-section">
              <h6><i className="fas fa-cog me-2"></i></h6>
              
              <Row className="mb-3">
                <Col md={6}>
                  <Form.Label></Form.Label>
                  <InputGroup>
                    <Form.Control
                      type="number"
                      step="0.01"
                      {...register('equipment_price', { 
                        required: '',
                        min: { value: 0, message: '' }
                      })}
                      isInvalid={!!errors.equipment_price}
                    />
                    <InputGroup.Text></InputGroup.Text>
                  </InputGroup>
                  {errors.equipment_price && (
                    <Form.Control.Feedback type="invalid">
                      {errors.equipment_price.message}
                    </Form.Control.Feedback>
                  )}
                </Col>
                <Col md={6}>
                  <Form.Label></Form.Label>
                  <InputGroup>
                    <Form.Control
                      type="number"
                      step="0.01"
                      {...register('down_payment', { 
                        min: { value: 0, message: '' }
                      })}
                      isInvalid={!!errors.down_payment}
                    />
                    <InputGroup.Text></InputGroup.Text>
                  </InputGroup>
                  {errors.down_payment && (
                    <Form.Control.Feedback type="invalid">
                      {errors.down_payment.message}
                    </Form.Control.Feedback>
                  )}
                </Col>
              </Row>

              <Row className="mb-3">
                <Col md={6}>
                  <Form.Label></Form.Label>
                  <InputGroup>
                    <Form.Control
                      type="number"
                      {...register('pv')}
                      readOnly
                      className="bg-light"
                    />
                    <InputGroup.Text></InputGroup.Text>
                  </InputGroup>
                  <Form.Text className="text-muted">
                     - 
                  </Form.Text>
                </Col>
                <Col md={6}>
                  <Form.Label>
                    {watchCalculationType === 'find_rate' ? '' : 'IRR'}
                  </Form.Label>
                  <InputGroup>
                    {watchCalculationType === 'find_rate' ? (
                      <Form.Control
                        type="number"
                        step="0.01"
                        {...register('target_pmt', { 
                          required: '',
                          min: { value: 1, message: '0' }
                        })}
                        isInvalid={!!errors.target_pmt}
                        placeholder="30000"
                      />
                    ) : (
                      <Form.Control
                        type="number"
                        step="0.01"
                        {...register('target_irr', { 
                          required: 'IRR',
                          min: { value: 0.01, message: 'IRR0' }
                        })}
                        isInvalid={!!errors.target_irr}
                        placeholder="8.00"
                      />
                    )}
                    <InputGroup.Text>
                      {watchCalculationType === 'find_rate' ? '' : '%'}
                    </InputGroup.Text>
                  </InputGroup>
                  {errors.target_pmt && (
                    <Form.Control.Feedback type="invalid">
                      {errors.target_pmt.message}
                    </Form.Control.Feedback>
                  )}
                  {errors.target_irr && (
                    <Form.Control.Feedback type="invalid">
                      {errors.target_irr.message}
                    </Form.Control.Feedback>
                  )}
                </Col>
              </Row>

              <Row className="mb-3">
                <Col md={4}>
                  <Form.Label></Form.Label>
                  <InputGroup>
                    <Form.Control
                      type="number"
                      step="0.1"
                      {...register('years', { 
                        required: '',
                        min: { value: 0.1, message: '0.1' }
                      })}
                      isInvalid={!!errors.years}
                    />
                    <InputGroup.Text></InputGroup.Text>
                  </InputGroup>
                  {errors.years && (
                    <Form.Control.Feedback type="invalid">
                      {errors.years.message}
                    </Form.Control.Feedback>
                  )}
                </Col>
                <Col md={4}>
                  <Form.Label></Form.Label>
                  <Form.Select
                    {...register('frequency', { required: '' })}
                    isInvalid={!!errors.frequency}
                  >
                    {frequencyOptions.map(option => (
                      <option key={option.value} value={option.value}>
                        {option.label}
                      </option>
                    ))}
                  </Form.Select>
                  {errors.frequency && (
                    <Form.Control.Feedback type="invalid">
                      {errors.frequency.message}
                    </Form.Control.Feedback>
                  )}
                </Col>
                <Col md={4}>
                  <Form.Label></Form.Label>
                  <InputGroup>
                    <Form.Control
                      type="number"
                      {...register('periods')}
                      readOnly
                      className="bg-light"
                    />
                    <InputGroup.Text></InputGroup.Text>
                  </InputGroup>
                  <Form.Text className="text-muted">
                    
                  </Form.Text>
                </Col>
              </Row>
            </div>

            {/*  */}
            <div className="form-section">
              <h6><i className="fas fa-shield-alt me-2"></i></h6>
              
              <Row className="mb-3">
                <Col md={6}>
                  <Form.Label></Form.Label>
                  <InputGroup>
                    <Form.Control
                      type="number"
                      step="0.01"
                      {...register('guarantee', { 
                        min: { value: 0, message: '' }
                      })}
                      isInvalid={!!errors.guarantee}
                    />
                    <InputGroup.Text></InputGroup.Text>
                  </InputGroup>
                  {errors.guarantee && (
                    <Form.Control.Feedback type="invalid">
                      {errors.guarantee.message}
                    </Form.Control.Feedback>
                  )}
                </Col>
                <Col md={6}>
                  <Form.Label></Form.Label>
                  <Form.Select
                    {...register('guarantee_mode')}
                  >
                    {guaranteeModeOptions.map(option => (
                      <option key={option.value} value={option.value}>
                        {option.label}
                      </option>
                    ))}
                  </Form.Select>
                </Col>
              </Row>
            </div>

            {/*  */}
            <Row className="mb-3">
              <Col>
                <div className="d-grid">
                  <Button 
                    type="submit" 
                    variant="info" 
                    size="lg"
                    disabled={loading}
                    className="btn-reverse-calculate"
                  >
                    {loading ? (
                      <>
                        <i className="fas fa-spinner fa-spin me-2"></i>
                        ...
                      </>
                    ) : (
                      <>
                        <i className="fas fa-exchange-alt me-2"></i>
                        
                      </>
                    )}
                  </Button>
                </div>
              </Col>
            </Row>
          </Form>
        </Card.Body>
      </Card>

      {/*  */}
      {result && (
        <Card>
          <Card.Header className="bg-success text-white">
            <h5 className="mb-0">
              <i className="fas fa-check-circle me-2"></i>
              
            </h5>
          </Card.Header>
          <Card.Body>
            <Alert variant="info">
              <Alert.Heading>
                <i className="fas fa-info-circle me-2"></i>
                
              </Alert.Heading>
              {watchCalculationType === 'find_rate' ? (
                <p></p>
              ) : (
                <p>IRR</p>
              )}
            </Alert>

            <Row>
              <Col lg={6}>
                <Card className="h-100">
                  <Card.Header className="bg-light">
                    <h6 className="mb-0"></h6>
                  </Card.Header>
                  <Card.Body>
                    <Table borderless>
                      <tbody>
                        {watchCalculationType === 'find_rate' ? (
                          <>
                            <tr>
                              <td><strong></strong></td>
                              <td className="text-end">
                                <Badge bg="primary" className="fs-6">
                                  {formatPercent(result.calculated_rate)}
                                </Badge>
                              </td>
                            </tr>
                            <tr>
                              <td><strong></strong></td>
                              <td className="text-end">{formatCurrency(result.target_pmt)}</td>
                            </tr>
                            <tr>
                              <td><strong></strong></td>
                              <td className="text-end">{formatCurrency(result.actual_pmt)}</td>
                            </tr>
                            <tr>
                              <td><strong></strong></td>
                              <td className="text-end">
                                <span className={Math.abs(result.error || 0) < 0.01 ? 'text-success' : 'text-warning'}>
                                  {formatCurrency(Math.abs(result.error || 0))}
                                </span>
                              </td>
                            </tr>
                          </>
                        ) : (
                          <>
                            <tr>
                              <td><strong></strong></td>
                              <td className="text-end">
                                <Badge bg="primary" className="fs-6">
                                  {formatCurrency(result.calculated_pmt)}
                                </Badge>
                              </td>
                            </tr>
                            <tr>
                              <td><strong>IRR</strong></td>
                              <td className="text-end">{formatPercent(result.target_irr)}</td>
                            </tr>
                            <tr>
                              <td><strong>IRR</strong></td>
                              <td className="text-end">{formatPercent(result.actual_irr)}</td>
                            </tr>
                            <tr>
                              <td><strong></strong></td>
                              <td className="text-end">
                                <span className={Math.abs(result.irr_error || 0) < 0.0001 ? 'text-success' : 'text-warning'}>
                                  {formatPercent(Math.abs(result.irr_error || 0))}
                                </span>
                              </td>
                            </tr>
                          </>
                        )}
                        <tr>
                          <td><strong></strong></td>
                          <td className="text-end">{formatCurrency(result.total_interest)}</td>
                        </tr>
                        <tr>
                          <td><strong></strong></td>
                          <td className="text-end">{formatCurrency(result.total_payment)}</td>
                        </tr>
                      </tbody>
                    </Table>
                  </Card.Body>
                </Card>
              </Col>
              <Col lg={6}>
                <Card className="h-100">
                  <Card.Header className="bg-light">
                    <h6 className="mb-0"></h6>
                  </Card.Header>
                  <Card.Body>
                    <Table borderless>
                      <tbody>
                        <tr>
                          <td><strong></strong></td>
                          <td className="text-end">{methodOptions.find(m => m.value === result.method)?.label}</td>
                        </tr>
                        <tr>
                          <td><strong></strong></td>
                          <td className="text-end">{formatCurrency(result.pv)}</td>
                        </tr>
                        <tr>
                          <td><strong></strong></td>
                          <td className="text-end">{result.periods}</td>
                        </tr>
                        <tr>
                          <td><strong></strong></td>
                          <td className="text-end">{frequencyOptions.find(f => f.value === result.frequency)?.label}</td>
                        </tr>
                        {result.guarantee > 0 && (
                          <>
                            <tr>
                              <td><strong></strong></td>
                              <td className="text-end">{formatCurrency(result.guarantee)}</td>
                            </tr>
                            <tr>
                              <td><strong></strong></td>
                              <td className="text-end">{result.guarantee_mode}</td>
                            </tr>
                          </>
                        )}
                      </tbody>
                    </Table>
                  </Card.Body>
                </Card>
              </Col>
            </Row>

            {result.iterations && (
              <Alert variant="secondary" className="mt-3">
                <small>
                  <i className="fas fa-info-circle me-1"></i>
                   {result.iterations} 
                </small>
              </Alert>
            )}
          </Card.Body>
        </Card>
      )}
    </div>
  );
};

export default ReverseCalculation;
