import React, { useEffect } from 'react';
import { Form, Button, Row, Col, Card, InputGroup } from 'react-bootstrap';
import { useForm } from 'react-hook-form';

const CalculatorForm = ({ onCalculate, loading, onAddToCompare }) => {
  const { register, handleSubmit, watch, setValue, formState: { errors } } = useForm({
    defaultValues: {
      method: 'equal_annuity',
      pv: 1000000,
      annual_rate: 8.00,
      periods: 36,
      frequency: 12,
      guarantee: 0,
      guarantee_mode: '',
      equipment_price: 1000000,
      down_payment: 200000,
      years: 3
    }
  });

  const watchMethod = watch('method');
  const watchEquipmentPrice = watch('equipment_price');
  const watchDownPayment = watch('down_payment');
  const watchFrequency = watch('frequency');
  const watchYears = watch('years');

  // 
  useEffect(() => {
    const equipmentPrice = parseFloat(watchEquipmentPrice) || 0;
    const downPayment = parseFloat(watchDownPayment) || 0;
    const pv = equipmentPrice - downPayment;
    setValue('pv', pv);
  }, [watchEquipmentPrice, watchDownPayment, setValue]);

  // 
  useEffect(() => {
    const years = parseFloat(watchYears) || 3;
    const frequency = parseInt(watchFrequency) || 12;
    //  =   
    const periods = years * frequency;
    setValue('periods', periods);
  }, [watchYears, watchFrequency, setValue]);

  const onSubmit = (data) => {
    // 
    const formData = {
      ...data,
      pv: parseFloat(data.pv),
      annual_rate: parseFloat(data.annual_rate) / 100, // 
      periods: parseInt(data.periods),
      frequency: parseInt(data.frequency),
      guarantee: parseFloat(data.guarantee) || 0,
      years: parseFloat(data.years) || data.periods / data.frequency
    };

    // 
    if (data.method === 'floating_rate' && data.rate_reset_schedule) {
      try {
        formData.rate_reset_schedule = JSON.parse(data.rate_reset_schedule);
      } catch (e) {
        formData.rate_reset_schedule = [];
      }
    }

    onCalculate(formData);
  };

  const methodOptions = [
    { value: 'equal_annuity', label: '' },
    { value: 'equal_principal', label: '' },
    { value: 'flat_rate', label: '' },
    { value: 'floating_rate', label: '' }
  ];

  const frequencyOptions = [
    { value: 12, label: '' },
    { value: 4, label: '' },
    { value: 2, label: '' },
    { value: 1, label: '' }
  ];

  const guaranteeModeOptions = [
    { value: '', label: '' },
    { value: '', label: '' },
    { value: '', label: '' }
  ];

  return (
    <Form onSubmit={handleSubmit(onSubmit)} className="calculator-form">
      {/*  */}
      <div className="form-section">
        <h6><i className="fas fa-cog me-2"></i></h6>
        
        <Row className="mb-3">
          <Col>
            <Form.Label></Form.Label>
            <Form.Select
              {...register('method', { required: '' })}
              isInvalid={!!errors.method}
            >
              {methodOptions.map(option => (
                <option key={option.value} value={option.value}>
                  {option.label}
                </option>
              ))}
            </Form.Select>
            {errors.method && (
              <Form.Control.Feedback type="invalid">
                {errors.method.message}
              </Form.Control.Feedback>
            )}
          </Col>
        </Row>

        <Row className="mb-3">
          <Col md={6}>
            <Form.Label></Form.Label>
            <InputGroup>
              <Form.Control
                type="number"
                step="0.01"
                {...register('equipment_price', { 
                  required: '',
                  min: { value: 0, message: '' }
                })}
                isInvalid={!!errors.equipment_price}
              />
              <InputGroup.Text></InputGroup.Text>
            </InputGroup>
            {errors.equipment_price && (
              <Form.Control.Feedback type="invalid">
                {errors.equipment_price.message}
              </Form.Control.Feedback>
            )}
          </Col>
          <Col md={6}>
            <Form.Label></Form.Label>
            <InputGroup>
              <Form.Control
                type="number"
                step="0.01"
                {...register('down_payment', { 
                  min: { value: 0, message: '' }
                })}
                isInvalid={!!errors.down_payment}
              />
              <InputGroup.Text></InputGroup.Text>
            </InputGroup>
            {errors.down_payment && (
              <Form.Control.Feedback type="invalid">
                {errors.down_payment.message}
              </Form.Control.Feedback>
            )}
          </Col>
        </Row>

        <Row className="mb-3">
          <Col>
            <Form.Label>PV</Form.Label>
            <InputGroup>
              <Form.Control
                type="number"
                step="0.01"
                {...register('pv', { 
                  required: '',
                  min: { value: 1, message: '0' }
                })}
                isInvalid={!!errors.pv}
                readOnly
              />
              <InputGroup.Text></InputGroup.Text>
            </InputGroup>
            {errors.pv && (
              <Form.Control.Feedback type="invalid">
                {errors.pv.message}
              </Form.Control.Feedback>
            )}
          </Col>
        </Row>

        <Row className="mb-3">
          <Col md={6}>
            <Form.Label>
              {watchMethod === 'flat_rate' ? '' : ''}
            </Form.Label>
            <InputGroup>
              <Form.Control
                type="number"
                step="0.01"
                {...register('annual_rate', { 
                  required: '',
                  min: { value: 0.01, message: '0' },
                  max: { value: 100, message: '100%' }
                })}
                isInvalid={!!errors.annual_rate}
              />
              <InputGroup.Text>%</InputGroup.Text>
            </InputGroup>
            <Form.Text className="text-muted">
              8%8.00
            </Form.Text>
            {errors.annual_rate && (
              <Form.Control.Feedback type="invalid">
                {errors.annual_rate.message}
              </Form.Control.Feedback>
            )}
          </Col>
          <Col md={6}>
            <Form.Label></Form.Label>
            <Form.Select
              {...register('frequency', { required: '' })}
              isInvalid={!!errors.frequency}
            >
              {frequencyOptions.map(option => (
                <option key={option.value} value={option.value}>
                  {option.label}
                </option>
              ))}
            </Form.Select>
            {errors.frequency && (
              <Form.Control.Feedback type="invalid">
                {errors.frequency.message}
              </Form.Control.Feedback>
            )}
          </Col>
        </Row>

        <Row className="mb-3">
          <Col md={6}>
            <Form.Label></Form.Label>
            <InputGroup>
              <Form.Control
                type="number"
                step="0.1"
                {...register('years', { 
                  required: '',
                  min: { value: 0.1, message: '0.1' }
                })}
                isInvalid={!!errors.years}
              />
              <InputGroup.Text></InputGroup.Text>
            </InputGroup>
            {errors.years && (
              <Form.Control.Feedback type="invalid">
                {errors.years.message}
              </Form.Control.Feedback>
            )}
          </Col>
          <Col md={6}>
            <Form.Label></Form.Label>
            <InputGroup>
              <Form.Control
                type="number"
                {...register('periods')}
                readOnly
              />
              <InputGroup.Text></InputGroup.Text>
            </InputGroup>
            <Form.Text className="text-muted">
              
            </Form.Text>
          </Col>
        </Row>

      </div>

      {/*  */}
      <div className="form-section">
        <h6><i className="fas fa-shield-alt me-2"></i></h6>
        
        <Row className="mb-3">
          <Col md={6}>
            <Form.Label></Form.Label>
            <InputGroup>
              <Form.Control
                type="number"
                step="0.01"
                {...register('guarantee', { 
                  min: { value: 0, message: '' }
                })}
                isInvalid={!!errors.guarantee}
              />
              <InputGroup.Text></InputGroup.Text>
            </InputGroup>
            {errors.guarantee && (
              <Form.Control.Feedback type="invalid">
                {errors.guarantee.message}
              </Form.Control.Feedback>
            )}
          </Col>
          <Col md={6}>
            <Form.Label></Form.Label>
            <Form.Select
              {...register('guarantee_mode')}
            >
              {guaranteeModeOptions.map(option => (
                <option key={option.value} value={option.value}>
                  {option.label}
                </option>
              ))}
            </Form.Select>
          </Col>
        </Row>
      </div>

      {/*  */}
      {watchMethod === 'floating_rate' && (
        <div className="form-section">
          <h6><i className="fas fa-chart-line me-2"></i></h6>
          
          <Row className="mb-3">
            <Col>
              <Form.Label>JSON</Form.Label>
              <Form.Control
                as="textarea"
                rows={3}
                {...register('rate_reset_schedule')}
                placeholder='[{"period": 6, "new_rate": 0.065}, {"period": 12, "new_rate": 0.07}]'
              />
              <Form.Text className="text-muted">
                66.5%127%
              </Form.Text>
            </Col>
          </Row>
        </div>
      )}

      {/*  */}
      <Row className="mb-3">
        <Col md={8}>
          <div className="d-grid">
            <Button 
              type="submit" 
              variant="primary" 
              size="lg"
              disabled={loading}
              className="btn-calculate"
            >
              {loading ? (
                <>
                  <i className="fas fa-spinner fa-spin me-2"></i>
                  ...
                </>
              ) : (
                <>
                  <i className="fas fa-calculator me-2"></i>
                  
                </>
              )}
            </Button>
          </div>
        </Col>
        <Col md={4}>
          <div className="d-grid">
            <Button 
              type="button"
              variant="outline-success" 
              size="lg"
              onClick={() => {
                const formData = {
                  method: watch('method'),
                  pv: parseFloat(watch('pv')),
                  annual_rate: parseFloat(watch('annual_rate')) / 100,
                  periods: parseInt(watch('periods')),
                  frequency: parseInt(watch('frequency')),
                  guarantee: parseFloat(watch('guarantee')) || 0,
                  guarantee_mode: watch('guarantee_mode'),
                  years: parseFloat(watch('years'))
                };
                if (onAddToCompare) {
                  onAddToCompare(formData);
                }
              }}
              className="btn-add-compare"
            >
              <i className="fas fa-plus me-2"></i>
              
            </Button>
          </div>
        </Col>
      </Row>

      {/*  */}
      <Card className="mt-3" bg="light">
        <Card.Body>
          <Card.Title className="h6">
            <i className="fas fa-info-circle me-2"></i>
            
          </Card.Title>
          <Card.Text className="small">
            {watchMethod === 'equal_annuity' && 
              'PMT = PV  [i  (1+i)^n] / [(1+i)^n - 1]'
            }
            {watchMethod === 'equal_principal' && 
              ''
            }
            {watchMethod === 'flat_rate' && 
              ''
            }
            {watchMethod === 'floating_rate' && 
              'LPR'
            }
          </Card.Text>
        </Card.Body>
      </Card>
    </Form>
  );
};

export default CalculatorForm;
