import React, { useState } from 'react';
import { Container, Row, Col, Card, Navbar, Nav, Alert } from 'react-bootstrap';
import { BrowserRouter as Router, Routes, Route, Link, useNavigate } from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';

import CalculatorForm from './components/CalculatorForm';
import ResultsDisplay from './components/ResultsDisplay';
import CompareSchemes from './components/CompareSchemes';
import SensitivityAnalysis from './components/SensitivityAnalysis';
import ReverseCalculation from './components/ReverseCalculation';
import api from './services/api';

import './App.css';

function AppContent() {
  const [activeTab, setActiveTab] = useState('calculator');
  const [calculationResult, setCalculationResult] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [compareSchemes, setCompareSchemes] = useState([]);
  const navigate = useNavigate();

  const handleCalculation = async (formData) => {
    setLoading(true);
    setError(null);
    
    try {
      const response = await api.post('/calculate', formData);
      setCalculationResult(response.data.data);
    } catch (err) {
      setError(err.response?.data?.message || '');
      console.error('Calculation error:', err);
    } finally {
      setLoading(false);
    }
  };

  const handleAddToCompare = (formData) => {
    const methodNames = {
      'equal_annuity': '',
      'equal_principal': '',
      'flat_rate': '',
      'floating_rate': ''
    };

    const newScheme = {
      id: Date.now(),
      name: `${compareSchemes.length + 1}${methodNames[formData.method]}`,
      method: formData.method,
      params: formData
    };

    if (compareSchemes.length >= 5) {
      setError('5');
      return;
    }

    setCompareSchemes([...compareSchemes, newScheme]);
    setActiveTab('compare');
    
    // 
    navigate('/compare');
  };

  const handleExport = async (format, data) => {
    try {
      const response = await api.post(`/export/${format}`, data, {
        responseType: 'blob'
      });
      
      // 
      const url = window.URL.createObjectURL(new Blob([response.data]));
      const link = document.createElement('a');
      link.href = url;
      
      const timestamp = new Date().toISOString().replace(/[:.]/g, '-').slice(0, 19);
      const extension = format === 'excel' ? 'xlsx' : 'json';
      link.setAttribute('download', `_${timestamp}.${extension}`);
      
      document.body.appendChild(link);
      link.click();
      link.remove();
      window.URL.revokeObjectURL(url);
    } catch (err) {
      setError(`${format}: ${err.response?.data?.message || err.message}`);
    }
  };

  return (
    <div className="App">
      <Navbar bg="primary" variant="dark" expand="lg" className="mb-4">
        <Container>
          <Navbar.Brand href="#home">
            <i className="fas fa-calculator me-2"></i>
            
          </Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="me-auto">
              <Nav.Link 
                as={Link} 
                to="/calculator" 
                active={activeTab === 'calculator'}
                onClick={() => setActiveTab('calculator')}
              >
                
              </Nav.Link>
              <Nav.Link 
                as={Link} 
                to="/reverse" 
                active={activeTab === 'reverse'}
                onClick={() => setActiveTab('reverse')}
              >
                
              </Nav.Link>
              <Nav.Link 
                as={Link} 
                to="/compare" 
                active={activeTab === 'compare'}
                onClick={() => setActiveTab('compare')}
              >
                
              </Nav.Link>
              <Nav.Link 
                as={Link} 
                to="/sensitivity" 
                active={activeTab === 'sensitivity'}
                onClick={() => setActiveTab('sensitivity')}
              >
                
              </Nav.Link>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>

      <Container fluid>
        {error && (
          <Alert variant="danger" dismissible onClose={() => setError(null)}>
            <Alert.Heading></Alert.Heading>
            {error}
          </Alert>
        )}

        <Routes>
          <Route path="/" element={
            <Row>
              <Col lg={4}>
                <Card className="h-100">
                  <Card.Header className="bg-primary text-white">
                    <h5 className="mb-0"></h5>
                  </Card.Header>
                  <Card.Body>
                    <CalculatorForm 
                      onCalculate={handleCalculation}
                      loading={loading}
                      onAddToCompare={handleAddToCompare}
                    />
                  </Card.Body>
                </Card>
              </Col>
              <Col lg={8}>
                <ResultsDisplay 
                  result={calculationResult}
                  loading={loading}
                  onExport={handleExport}
                />
              </Col>
            </Row>
          } />
          
          <Route path="/calculator" element={
            <Row>
              <Col lg={4}>
                <Card className="h-100">
                  <Card.Header className="bg-primary text-white">
                    <h5 className="mb-0"></h5>
                  </Card.Header>
                  <Card.Body>
                    <CalculatorForm 
                      onCalculate={handleCalculation}
                      loading={loading}
                      onAddToCompare={handleAddToCompare}
                    />
                  </Card.Body>
                </Card>
              </Col>
              <Col lg={8}>
                <ResultsDisplay 
                  result={calculationResult}
                  loading={loading}
                  onExport={handleExport}
                />
              </Col>
            </Row>
          } />
          
          <Route path="/reverse" element={
            <ReverseCalculation onError={setError} />
          } />
          
          <Route path="/compare" element={
            <CompareSchemes 
              onError={setError} 
              initialSchemes={compareSchemes}
            />
          } />
          
          <Route path="/sensitivity" element={
            <SensitivityAnalysis onError={setError} />
          } />
        </Routes>
      </Container>

      <footer className="bg-light text-center py-3 mt-5">
        <Container>
          <p className="text-muted mb-0">
             2025  | 
            <a href="https://blog.52trz.com" className="text-decoration-none ms-2">
              
            </a>
          </p>
        </Container>
      </footer>
    </div>
  );
}

function App() {
  return (
    <Router>
      <AppContent />
    </Router>
  );
}

export default App;
