import React, { useState } from 'react';
import { Card, Row, Col, Button, Form, Table, Alert, Badge, Modal } from 'react-bootstrap';
import { useForm } from 'react-hook-form';
import api from '../services/api';

const CompareSchemes = ({ onError, initialSchemes = [] }) => {
  const [schemes, setSchemes] = useState(initialSchemes);
  const [comparisonResult, setComparisonResult] = useState(null);
  const [loading, setLoading] = useState(false);
  const [showModal, setShowModal] = useState(false);

  const { register, handleSubmit, reset, formState: { errors } } = useForm();

  // 
  React.useEffect(() => {
    if (initialSchemes.length > 0) {
      setSchemes(initialSchemes);
    }
  }, [initialSchemes]);

  const addScheme = (data) => {
    const years = parseFloat(data.years) || 3;
    const frequency = parseInt(data.frequency) || 12;
    const periods = years * frequency;

    const newScheme = {
      id: Date.now(),
      name: data.name,
      method: data.method,
      params: {
        pv: parseFloat(data.pv),
        annual_rate: parseFloat(data.annual_rate) / 100, // 
        periods: periods, // 
        frequency: frequency,
        guarantee: parseFloat(data.guarantee) || 0,
        guarantee_mode: data.guarantee_mode || '',
        years: years
      }
    };

    if (schemes.length >= 5) {
      onError('5');
      return;
    }

    setSchemes([...schemes, newScheme]);
    setShowModal(false);
    reset();
  };

  // 
  const addSchemeFromExternal = (schemeData) => {
    const methodNames = {
      'equal_annuity': '',
      'equal_principal': '',
      'flat_rate': '',
      'floating_rate': ''
    };

    const newScheme = {
      id: Date.now(),
      name: `${schemes.length + 1}${methodNames[schemeData.method]}`,
      method: schemeData.method,
      params: schemeData
    };

    if (schemes.length >= 5) {
      onError('5');
      return false;
    }

    setSchemes([...schemes, newScheme]);
    return true;
  };

  // 
  React.useImperativeHandle(React.forwardRef(() => null), () => ({
    addScheme: addSchemeFromExternal
  }));

  const removeScheme = (id) => {
    setSchemes(schemes.filter(scheme => scheme.id !== id));
    if (schemes.length <= 1) {
      setComparisonResult(null);
    }
  };

  const compareSchemes = async () => {
    if (schemes.length < 2) {
      onError('2');
      return;
    }

    setLoading(true);
    try {
      const response = await api.post('/compare', { schemes });
      setComparisonResult(response.data.data);
    } catch (error) {
      onError(error.response?.data?.message || '');
    } finally {
      setLoading(false);
    }
  };

  const formatCurrency = (amount) => {
    return new Intl.NumberFormat('zh-CN', {
      style: 'currency',
      currency: 'CNY',
      minimumFractionDigits: 2
    }).format(amount || 0);
  };

  const formatPercent = (rate) => {
    return `${((rate || 0) * 100).toFixed(4)}%`;
  };

  const getMethodLabel = (method) => {
    const labels = {
      'equal_annuity': '',
      'equal_principal': '',
      'flat_rate': '',
      'floating_rate': ''
    };
    return labels[method] || method;
  };

  const getBestScheme = (metric) => {
    if (!comparisonResult || comparisonResult.length === 0) return null;
    
    let bestIndex = 0;
    let bestValue = comparisonResult[0][metric];

    comparisonResult.forEach((result, index) => {
      if (metric === 'total_interest' || metric === 'total_payment') {
        // 
        if (result[metric] < bestValue) {
          bestValue = result[metric];
          bestIndex = index;
        }
      } else if (metric === 'irr') {
        // 
        if (result[metric] > bestValue) {
          bestValue = result[metric];
          bestIndex = index;
        }
      }
    });

    return bestIndex;
  };

  return (
    <div>
      {/*  */}
      <Card className="mb-4">
        <Card.Header className="d-flex justify-content-between align-items-center">
          <h5 className="mb-0">
            <i className="fas fa-layer-group me-2"></i>
            
          </h5>
          <Button 
            variant="primary" 
            size="sm"
            onClick={() => setShowModal(true)}
            disabled={schemes.length >= 5}
          >
            <i className="fas fa-plus me-1"></i>
            
          </Button>
        </Card.Header>
        <Card.Body>
          {schemes.length === 0 ? (
            <Alert variant="info" className="text-center mb-0">
              <i className="fas fa-info-circle fa-2x mb-3"></i>
              <h6></h6>
              <p className="mb-0">""</p>
            </Alert>
          ) : (
            <Row>
              {schemes.map((scheme) => (
                <Col md={6} lg={4} key={scheme.id} className="mb-3">
                  <Card className="scheme-card h-100">
                    <Card.Header className="d-flex justify-content-between align-items-center">
                      <h6 className="mb-0">{scheme.name}</h6>
                      <Button
                        variant="outline-danger"
                        size="sm"
                        onClick={() => removeScheme(scheme.id)}
                      >
                        <i className="fas fa-trash"></i>
                      </Button>
                    </Card.Header>
                    <Card.Body>
                      <div className="mb-2">
                        <Badge bg="info">{getMethodLabel(scheme.method)}</Badge>
                      </div>
                      <Table borderless size="sm">
                        <tbody>
                          <tr>
                            <td></td>
                            <td>{formatCurrency(scheme.params.pv)}</td>
                          </tr>
                          <tr>
                            <td></td>
                            <td>{(scheme.params.annual_rate * 100).toFixed(2)}%</td>
                          </tr>
                          <tr>
                            <td></td>
                            <td>{scheme.params.periods}</td>
                          </tr>
                          {scheme.params.guarantee > 0 && (
                            <>
                              <tr>
                                <td></td>
                                <td>{formatCurrency(scheme.params.guarantee)}</td>
                              </tr>
                              <tr>
                                <td></td>
                                <td><Badge bg="secondary">{scheme.params.guarantee_mode}</Badge></td>
                              </tr>
                            </>
                          )}
                        </tbody>
                      </Table>
                    </Card.Body>
                  </Card>
                </Col>
              ))}
            </Row>
          )}
          
          {schemes.length >= 2 && (
            <div className="text-center mt-3">
              <Button
                variant="success"
                size="lg"
                onClick={compareSchemes}
                disabled={loading}
              >
                {loading ? (
                  <>
                    <i className="fas fa-spinner fa-spin me-2"></i>
                    ...
                  </>
                ) : (
                  <>
                    <i className="fas fa-balance-scale me-2"></i>
                    
                  </>
                )}
              </Button>
            </div>
          )}
        </Card.Body>
      </Card>

      {/*  */}
      {comparisonResult && (
        <Card>
          <Card.Header>
            <h5 className="mb-0">
              <i className="fas fa-chart-bar me-2"></i>
              
            </h5>
          </Card.Header>
          <Card.Body>
            <div className="table-responsive">
              <Table striped bordered hover>
                <thead className="table-primary">
                  <tr>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th>IRR</th>
                    <th></th>
                  </tr>
                </thead>
                <tbody>
                  {comparisonResult.map((result, index) => {
                    const isBestPayment = getBestScheme('pmt') === index;
                    const isBestInterest = getBestScheme('total_interest') === index;
                    const isBestTotal = getBestScheme('total_payment') === index;
                    const isBestIRR = getBestScheme('irr') === index;

                    return (
                      <tr key={index}>
                        <td>
                          <strong>{result.scheme_name}</strong>
                        </td>
                        <td>
                          <Badge bg="secondary">
                            {getMethodLabel(result.method)}
                          </Badge>
                        </td>
                        <td className={isBestPayment ? 'table-success' : ''}>
                          {formatCurrency(result.pmt)}
                          {isBestPayment && <i className="fas fa-crown text-warning ms-1"></i>}
                        </td>
                        <td className={isBestInterest ? 'table-success' : ''}>
                          {formatCurrency(result.total_interest)}
                          {isBestInterest && <i className="fas fa-crown text-warning ms-1"></i>}
                        </td>
                        <td className={isBestTotal ? 'table-success' : ''}>
                          {formatCurrency(result.total_payment)}
                          {isBestTotal && <i className="fas fa-crown text-warning ms-1"></i>}
                        </td>
                        <td className={isBestIRR ? 'table-success' : ''}>
                          {formatPercent(result.irr)}
                          {isBestIRR && <i className="fas fa-crown text-warning ms-1"></i>}
                        </td>
                        <td>
                          {(isBestPayment || isBestInterest || isBestTotal) && (
                            <Badge bg="success"></Badge>
                          )}
                        </td>
                      </tr>
                    );
                  })}
                </tbody>
              </Table>
            </div>

            {/*  */}
            <Alert variant="info">
              <Alert.Heading>
                <i className="fas fa-lightbulb me-2"></i>
                
              </Alert.Heading>
              <Row>
                <Col md={6}>
                  <h6></h6>
                  <p>
                    {comparisonResult[getBestScheme('pmt')]?.scheme_name}<br/>
                    {comparisonResult[getBestScheme('total_interest')]?.scheme_name}<br/>
                    {comparisonResult[getBestScheme('total_payment')]?.scheme_name}
                  </p>
                </Col>
                <Col md={6}>
                  <h6></h6>
                  <p>
                    IRR{comparisonResult[getBestScheme('irr')]?.scheme_name}<br/>
                    IRR{formatPercent(Math.max(...comparisonResult.map(r => r.irr)) - Math.min(...comparisonResult.map(r => r.irr)))}
                  </p>
                </Col>
              </Row>
            </Alert>
          </Card.Body>
        </Card>
      )}

      {/*  */}
      <Modal show={showModal} onHide={() => setShowModal(false)} size="lg">
        <Modal.Header closeButton>
          <Modal.Title></Modal.Title>
        </Modal.Header>
        <Form onSubmit={handleSubmit(addScheme)}>
          <Modal.Body>
            <Row className="mb-3">
              <Col md={6}>
                <Form.Label></Form.Label>
                <Form.Control
                  type="text"
                  {...register('name', { required: '' })}
                  isInvalid={!!errors.name}
                  placeholder="A"
                />
                {errors.name && (
                  <Form.Control.Feedback type="invalid">
                    {errors.name.message}
                  </Form.Control.Feedback>
                )}
              </Col>
              <Col md={6}>
                <Form.Label></Form.Label>
                <Form.Select
                  {...register('method', { required: '' })}
                  isInvalid={!!errors.method}
                >
                  <option value="">...</option>
                  <option value="equal_annuity"></option>
                  <option value="equal_principal"></option>
                  <option value="flat_rate"></option>
                </Form.Select>
                {errors.method && (
                  <Form.Control.Feedback type="invalid">
                    {errors.method.message}
                  </Form.Control.Feedback>
                )}
              </Col>
            </Row>

            <Row className="mb-3">
              <Col md={6}>
                <Form.Label></Form.Label>
                <Form.Control
                  type="number"
                  step="0.01"
                  {...register('pv', { 
                    required: '',
                    min: { value: 1, message: '0' }
                  })}
                  isInvalid={!!errors.pv}
                />
                {errors.pv && (
                  <Form.Control.Feedback type="invalid">
                    {errors.pv.message}
                  </Form.Control.Feedback>
                )}
              </Col>
              <Col md={6}>
                <Form.Label></Form.Label>
                <Form.Control
                  type="number"
                  step="0.01"
                  {...register('annual_rate', { 
                    required: '',
                    min: { value: 0.01, message: '0' },
                    max: { value: 100, message: '100%' }
                  })}
                  isInvalid={!!errors.annual_rate}
                  placeholder="8.00 (%)"
                />
                {errors.annual_rate && (
                  <Form.Control.Feedback type="invalid">
                    {errors.annual_rate.message}
                  </Form.Control.Feedback>
                )}
              </Col>
            </Row>

            <Row className="mb-3">
              <Col md={6}>
                <Form.Label></Form.Label>
                <Form.Select
                  {...register('frequency', { required: '' })}
                  isInvalid={!!errors.frequency}
                >
                  <option value="">...</option>
                  <option value="12"></option>
                  <option value="4"></option>
                  <option value="2"></option>
                  <option value="1"></option>
                </Form.Select>
                {errors.frequency && (
                  <Form.Control.Feedback type="invalid">
                    {errors.frequency.message}
                  </Form.Control.Feedback>
                )}
              </Col>
              <Col md={6}>
                <Form.Label></Form.Label>
                <Form.Control
                  type="number"
                  step="0.1"
                  {...register('years', { 
                    required: '',
                    min: { value: 0.1, message: '0.1' }
                  })}
                  isInvalid={!!errors.years}
                  placeholder="3.0"
                />
                {errors.years && (
                  <Form.Control.Feedback type="invalid">
                    {errors.years.message}
                  </Form.Control.Feedback>
                )}
              </Col>
            </Row>

            <Row className="mb-3">
              <Col md={6}>
                <Form.Label></Form.Label>
                <Form.Control
                  type="number"
                  step="0.01"
                  {...register('guarantee', { 
                    min: { value: 0, message: '' }
                  })}
                  isInvalid={!!errors.guarantee}
                  placeholder="0"
                />
                {errors.guarantee && (
                  <Form.Control.Feedback type="invalid">
                    {errors.guarantee.message}
                  </Form.Control.Feedback>
                )}
              </Col>
              <Col md={6}>
                <Form.Label></Form.Label>
                <Form.Select
                  {...register('guarantee_mode')}
                >
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                </Form.Select>
              </Col>
            </Row>
          </Modal.Body>
          <Modal.Footer>
            <Button variant="secondary" onClick={() => setShowModal(false)}>
              
            </Button>
            <Button variant="primary" type="submit">
              <i className="fas fa-plus me-1"></i>
              
            </Button>
          </Modal.Footer>
        </Form>
      </Modal>
    </div>
  );
};

export default CompareSchemes;
