import React, { useState, useEffect, useCallback } from 'react';
import { Card, Row, Col, Table, Button, Spinner, Alert, Badge, Tabs, Tab } from 'react-bootstrap';
import Plot from 'react-plotly.js';
import api from '../services/api';

const ResultsDisplay = ({ result, loading, onExport }) => {
  const [activeTab, setActiveTab] = useState('summary');
  const [charts, setCharts] = useState({});
  const [chartLoading, setChartLoading] = useState(false);

  const generateCharts = useCallback(async () => {
    if (!result || !result.schedule) return;

    setChartLoading(true);
    try {
      // 
      const paymentStructureResponse = await api.post('/charts/payment_structure', {
        schedule: result.schedule
      });
      
      // 
      const cashFlowResponse = await api.post('/charts/cash_flow', {
        schedule: result.schedule,
        initial_payment: result.pv || 0
      });

      setCharts({
        paymentStructure: JSON.parse(paymentStructureResponse.data.chart),
        cashFlow: JSON.parse(cashFlowResponse.data.chart)
      });
    } catch (error) {
      console.error(':', error);
    } finally {
      setChartLoading(false);
    }
  }, [result]);

  useEffect(() => {
    if (result && result.schedule) {
      generateCharts();
    }
  }, [result, generateCharts]);

  const formatCurrency = (amount) => {
    return new Intl.NumberFormat('zh-CN', {
      style: 'currency',
      currency: 'CNY',
      minimumFractionDigits: 2
    }).format(amount || 0);
  };

  const formatPercent = (rate) => {
    return `${((rate || 0) * 100).toFixed(4)}%`;
  };

  const handleExport = (format) => {
    if (!result) return;
    
    const exportData = {
      ...result,
      method: result.method,
      pv: result.pv,
      annual_rate: result.annual_rate,
      periods: result.periods,
      timestamp: new Date().toISOString()
    };
    
    onExport(format, exportData);
  };

  if (loading) {
    return (
      <Card>
        <Card.Body className="loading-spinner">
          <Spinner animation="border" variant="primary" />
          <div className="ms-3">...</div>
        </Card.Body>
      </Card>
    );
  }

  if (!result) {
    return (
      <Card>
        <Card.Body className="text-center text-muted py-5">
          <i className="fas fa-chart-bar fa-3x mb-3"></i>
          <h5></h5>
          <p>""</p>
        </Card.Body>
      </Card>
    );
  }

  const summaryMetrics = [
    {
      title: '',
      value: formatCurrency(result.pmt || result.schedule?.[0]?.payment),
      icon: 'fas fa-money-bill-wave',
      color: 'primary'
    },
    {
      title: '',
      value: formatCurrency(result.total_interest),
      icon: 'fas fa-percentage',
      color: 'info'
    },
    {
      title: '',
      value: formatCurrency(result.total_payment),
      icon: 'fas fa-calculator',
      color: 'success'
    },
    {
      title: 'IRR',
      value: formatPercent(result.irr),
      icon: 'fas fa-chart-line',
      color: 'warning'
    }
  ];

  return (
    <div className="results-section">
      {/*  */}
      <Card className="mb-3">
        <Card.Body>
          <Row className="align-items-center">
            <Col>
              <h5 className="mb-0">
                <i className="fas fa-chart-bar me-2"></i>
                
                <Badge bg="success" className="ms-2">{result.method}</Badge>
              </h5>
            </Col>
            <Col xs="auto">
              <Button 
                variant="outline-success" 
                size="sm" 
                className="btn-export"
                onClick={() => handleExport('excel')}
              >
                <i className="fas fa-file-excel me-1"></i>
                Excel
              </Button>
              <Button 
                variant="outline-primary" 
                size="sm" 
                className="btn-export"
                onClick={() => handleExport('json')}
              >
                <i className="fas fa-download me-1"></i>
                JSON
              </Button>
            </Col>
          </Row>
        </Card.Body>
      </Card>

      {/*  */}
      <Row className="mb-4">
        {summaryMetrics.map((metric, index) => (
          <Col md={3} key={index} className="mb-3">
            <Card className="metric-card h-100">
              <Card.Body className="text-center">
                <div className={`text-${metric.color} mb-2`}>
                  <i className={`${metric.icon} fa-2x`}></i>
                </div>
                <div className="metric-value">{metric.value}</div>
                <div className="metric-label">{metric.title}</div>
              </Card.Body>
            </Card>
          </Col>
        ))}
      </Row>

      {/*  */}
      <Card>
        <Card.Body>
          <Tabs 
            activeKey={activeTab} 
            onSelect={setActiveTab}
            className="mb-3"
          >
            {/*  */}
            <Tab eventKey="schedule" title="">
              <div className="payment-schedule-table">
                <Table striped bordered hover responsive>
                  <thead>
                    <tr>
                      <th></th>
                      <th></th>
                      <th></th>
                      <th></th>
                      <th></th>
                      <th></th>
                    </tr>
                  </thead>
                  <tbody>
                    {result.schedule?.map((payment, index) => (
                      <tr key={index}>
                        <td>{payment.period}</td>
                        <td className="text-end">
                          {formatCurrency(payment.payment)}
                        </td>
                        <td className="text-end">
                          {formatCurrency(payment.principal)}
                        </td>
                        <td className="text-end">
                          {formatCurrency(payment.interest)}
                        </td>
                        <td className="text-end">
                          {formatCurrency(payment.remaining_balance)}
                        </td>
                        <td>
                          <div className="progress" style={{ height: '20px' }}>
                            <div 
                              className="progress-bar bg-primary" 
                              style={{ 
                                width: `${(payment.principal / payment.payment * 100)}%` 
                              }}
                            />
                            <div 
                              className="progress-bar bg-info" 
                              style={{ 
                                width: `${(payment.interest / payment.payment * 100)}%` 
                              }}
                            />
                          </div>
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </Table>
              </div>
            </Tab>

            {/*  */}
            <Tab eventKey="charts" title="">
              {chartLoading ? (
                <div className="text-center py-4">
                  <Spinner animation="border" variant="primary" />
                  <div className="mt-2">...</div>
                </div>
              ) : (
                <Row>
                  <Col lg={6} className="mb-4">
                    <Card className="chart-container">
                      <Card.Body>
                        <Card.Title></Card.Title>
                        {charts.paymentStructure && (
                          <Plot
                            data={charts.paymentStructure.data}
                            layout={{
                              ...charts.paymentStructure.layout,
                              autosize: true,
                              height: 350
                            }}
                            config={{ 
                              displayModeBar: true,
                              responsive: true 
                            }}
                            style={{ width: '100%' }}
                          />
                        )}
                      </Card.Body>
                    </Card>
                  </Col>
                  <Col lg={6} className="mb-4">
                    <Card className="chart-container">
                      <Card.Body>
                        <Card.Title></Card.Title>
                        {charts.cashFlow && (
                          <Plot
                            data={charts.cashFlow.data}
                            layout={{
                              ...charts.cashFlow.layout,
                              autosize: true,
                              height: 350
                            }}
                            config={{ 
                              displayModeBar: true,
                              responsive: true 
                            }}
                            style={{ width: '100%' }}
                          />
                        )}
                      </Card.Body>
                    </Card>
                  </Col>
                </Row>
              )}
            </Tab>

            {/*  */}
            {result.guarantee_offset && (
              <Tab eventKey="guarantee" title="">
                <Alert variant="info">
                  <Alert.Heading>
                    <i className="fas fa-shield-alt me-2"></i>
                    
                  </Alert.Heading>
                  <Row>
                    <Col md={4}>
                      <strong></strong>
                      {formatCurrency(result.guarantee_offset.total_offset)}
                    </Col>
                    <Col md={4}>
                      <strong></strong>
                      {formatCurrency(result.guarantee_offset.unused_guarantee)}
                    </Col>
                    <Col md={4}>
                      <strong></strong>
                      {result.guarantee_offset.offset_details.length}
                    </Col>
                  </Row>
                </Alert>

                <Table striped bordered hover responsive>
                  <thead>
                    <tr>
                      <th></th>
                      <th></th>
                      <th></th>
                      <th></th>
                    </tr>
                  </thead>
                  <tbody>
                    {result.guarantee_offset.offset_details?.map((offset, index) => (
                      <tr key={index}>
                        <td>{offset.period}</td>
                        <td className="text-end">
                          {formatCurrency(offset.offset_amount)}
                        </td>
                        <td className="text-end">
                          {formatCurrency(offset.remaining_payment)}
                        </td>
                        <td className="text-end">
                          {((offset.offset_amount / (offset.offset_amount + offset.remaining_payment)) * 100).toFixed(2)}%
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </Table>
              </Tab>
            )}

            {/*  */}
            <Tab eventKey="details" title="">
              <Row>
                <Col md={6}>
                  <Card className="mb-3">
                    <Card.Header>
                      <h6 className="mb-0"></h6>
                    </Card.Header>
                    <Card.Body>
                      <Table borderless size="sm">
                        <tbody>
                          <tr>
                            <td><strong></strong></td>
                            <td>{result.method}</td>
                          </tr>
                          {result.flat_rate && (
                            <tr>
                              <td><strong></strong></td>
                              <td>{formatPercent(result.flat_rate)}</td>
                            </tr>
                          )}
                          {result.actual_irr && (
                            <tr>
                              <td><strong>IRR</strong></td>
                              <td>{formatPercent(result.actual_irr)}</td>
                            </tr>
                          )}
                          <tr>
                            <td><strong></strong></td>
                            <td>{result.schedule?.length}</td>
                          </tr>
                        </tbody>
                      </Table>
                    </Card.Body>
                  </Card>
                </Col>
                <Col md={6}>
                  <Card className="mb-3">
                    <Card.Header>
                      <h6 className="mb-0"></h6>
                    </Card.Header>
                    <Card.Body>
                      <Table borderless size="sm">
                        <tbody>
                          <tr>
                            <td><strong></strong></td>
                            <td>{formatCurrency(result.schedule?.[0]?.payment)}</td>
                          </tr>
                          <tr>
                            <td><strong></strong></td>
                            <td>{formatCurrency(result.schedule?.slice(-1)[0]?.payment)}</td>
                          </tr>
                          <tr>
                            <td><strong></strong></td>
                            <td>{formatCurrency(result.total_payment / result.schedule?.length)}</td>
                          </tr>
                          <tr>
                            <td><strong></strong></td>
                            <td>{((result.total_interest / result.total_payment) * 100).toFixed(2)}%</td>
                          </tr>
                        </tbody>
                      </Table>
                    </Card.Body>
                  </Card>
                </Col>
              </Row>
            </Tab>
          </Tabs>
        </Card.Body>
      </Card>
    </div>
  );
};

export default ResultsDisplay;
